<script setup>
    import { ref, onMounted } from "vue"

    import qds from "../services/QuestionDataService"

    const qlist = ref([0])

    const retrieveQuestion = () => {
        qds.getAll()
        .then(response => {
          qlist.value = response.data;
        //   console.log(response.data);
          console.log(qlist.value[0].id, qlist.value[0].subject, qlist.value[0].createDate)
        })
        .catch(e => {
          console.log(e);
        });
    }

    // const refreshList = () => {
    //     retrieveQuestion()
    // }

    // const removeAllQuestions = () => {
    //     return ""
    // }

    onMounted(() => {
        retrieveQuestion()
    })
</script>

<template>
    <table class="table table-striped">
        <thead>
            <tr>
                <th>Num</th>
                <th>제목</th>
                <th>작성일시</th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="qele in qlist" :key="qele.id" :title="qele.subject">
                <td>{{ qele.id }}</td>
                <td>{{ qele.subject }}</td>
                <td>{{ qele.createDate }}</td>
            </tr>
            
            <!-- <tr th:each="question : ${questionList}">
                <td th:text="${question.subject}"></td>
                <td th:text="${question.createDate}"></td>
            </tr> -->
        </tbody>
    </table>
</template>